// components/ui/navbar.tsx
'use client';
import { motion } from 'framer-motion';
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Moon, Sun } from 'lucide-react';
import { useTheme } from 'next-themes';
import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation'; // Import useRouter

export default function Navbar() {
  const { theme, setTheme } = useTheme();
  const [mounted, setMounted] = useState(false);
  const router = useRouter(); // Initialize useRouter

  useEffect(() => {
    setMounted(true);
  }, []);

  const navLinks = [
    { name: 'About', path: '/about' },
    { name: 'Projects', path: '/projects' },
    { name: 'Blogs', path: '/blogs' },
    // Removed the inline Contact link, it will be handled separately
  ];

  const scrollToContact = () => {
    router.push('/#contact'); // Use router.push for in-page navigation
  };


  return (
    <motion.nav
      initial={{ y: -100 }}
      animate={{ y: 0 }}
      className="fixed top-0 left-0 right-0 bg-background/80 backdrop-blur-md z-50 border-b"
    >
      <div className="container mx-auto px-4 md:px-8 flex justify-between items-center h-16">
        <motion.div whileHover={{ scale: 1.05 }}>
          <Link href="/" className="text-xl font-bold">
            Hriday Sehgal
          </Link>
        </motion.div>

        <div className="flex items-center gap-4">
          <div className="hidden md:flex gap-4">
            {navLinks.map((link) => (
              <motion.div
                key={link.name}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                <Button asChild variant="ghost">
                  <Link href={link.path}>{link.name}</Link>
                </Button>
              </motion.div>
            ))}
            {/* Dedicated Contact Button */}
            <motion.div
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
            >
                <Button variant="ghost" onClick={scrollToContact}> {/* Use onClick */}
                    Contact
                </Button>
            </motion.div>
          </div>

          <Button
            variant="outline"
            size="icon"
            onClick={() => setTheme(theme === 'dark' ? 'light' : 'dark')}
          >
            {mounted ? (
              theme === 'dark' ? (
                <Sun className="h-4 w-4" />
              ) : (
                <Moon className="h-4 w-4" />
              )
            ) : (
              <Sun className="h-4 w-4" />
            )}
          </Button>
        </div>
      </div>
    </motion.nav>
  );
}
